.review-card {
	background-color: #fff;
	border: 1px solid #ccc;
	padding: 1.2rem;
	margin: 1rem auto;
	border-radius: 10px;
	max-width: 500px;
	box-shadow: 0 4px 10px rgba(0, 0, 0, 0.08);
	font-family: sans-serif;
}

	.review-card h2 {
		margin-top: 0;
		font-size: 1.5rem;
		color: #333;
	}

	.review-card h2 {
		margin-top: 0;
		font-size: 1.3rem;
		color: #333;
	}

	.review-card p {
		margin: 0.5rem 0;
		line-height: 1.4;
	}
